// calculate using the YIQ color space:
// https://en.wikipedia.org/wiki/YIQ
@mixin text-color($color) {
    $red: red($color);
    $green: green($color);
    $blue: blue($color);
    $yiq: (($red * 299) + ($green * 587) + ($blue * 114)) / 1000;
    @if ($yiq >= 128) {
        color: #000000;
    } @else {
        color: #ffffff;
    }
}

